import { useState } from "react";
import Item from "./Item";
import { Drawer } from "antd";

interface Iprops {
    originMovieData: [];
    addCollect: (item: any) => void;
    removeCollect: (item: any) => void;
    keyWord: string;
    sortByTime: boolean;
    sortByRate: boolean;
}

export default function List({
    originMovieData,
    addCollect,
    removeCollect,
    keyWord,
    sortByTime,
    sortByRate,
}: Iprops) {
    const [open, setOpen] = useState(false);
    let [drawerData, setDrawerData] = useState<any>();

    const showDrawer = () => {
        setOpen(true);
    };

    const onClose = () => {
        setOpen(false);
    };

    function displayData() {
        let displayMoviesData = originMovieData.filter((item: any) => {
            if (item.title.indexOf(keyWord) !== -1) return true;
            else return false;
        });

        if (sortByTime) {
            displayMoviesData.sort(
                (a: any, b: any) =>
                    b.release_date.replaceAll(/-/g, "") -
                    a.release_date.replaceAll(/-/g, "")
            );
        }
        if (sortByRate) {
            displayMoviesData.sort(
                (a: any, b: any) => b.rating[0] - a.rating[0]
            );
        }

        return displayMoviesData.map((item, index) => {
            return (
                <Item
                    originDataObject={item}
                    key={index}
                    addCollect={addCollect}
                    removeCollect={removeCollect}
                    drawerDataFun={drawerDataFun}
                ></Item>
            );
        });
    }

    function drawerDataFun(item: any) {
        setDrawerData(item);
        showDrawer();
    }

    return (
        <div className="itemList">
            {/* {
                originMovieData.filter((item:any) => {
                    if (item.title.indexOf(keyWord) != -1) return true
                    else return false
                }).
                    map((item, index) => {
                    return (<Item originDataObject={item} key={index} addCollect={addCollect} removeCollect={removeCollect}></Item>)
                })
            } */}
            {displayData()}

            <Drawer
                title="detail message"
                placement="right"
                onClose={onClose}
                open={open}
                className="dreaP"
            >
                <p>{": " + drawerData?.title}</p>
                <p>{": " + drawerData?.release_date}</p>
                <p>{": " + drawerData?.rating[0]}</p>
            </Drawer>
        </div>
    );
}
