import { Card, Tooltip } from "antd";
import { PlusOutlined, LineOutlined } from "@ant-design/icons";

interface Iprops {
    originDataObject: any;
    addCollect: (item: any) => void;
    removeCollect: (item: any) => void;
    drawerDataFun: (item: any) => void;
}

export default function Item({
    originDataObject,
    addCollect,
    removeCollect,
    drawerDataFun,
}: Iprops) {
    return (
        <div className="cardItem">
            <Card
                hoverable
                className="card"
                cover={<img alt="example" src={originDataObject.cover_url} />}
                onClick={() => drawerDataFun(originDataObject)}
            >
                <p>
                    {originDataObject.release_date.slice(0, 4) +
                        " / " +
                        originDataObject.regions.join(" ") +
                        " / " +
                        originDataObject.types.join(" ")}
                </p>
                <p>
                    {originDataObject.title +
                        "(" +
                        originDataObject.score +
                        ")"}
                </p>

                {originDataObject.notOnCollect && (
                    <Tooltip title="">
                        <div
                            className="tag tagFirst"
                            onClick={(e) => {
                                e.stopPropagation();
                                addCollect(originDataObject.id);
                            }}
                        >
                            <span>
                                <PlusOutlined />
                            </span>
                        </div>
                    </Tooltip>
                )}
                {!originDataObject.notOnCollect && (
                    <Tooltip title="">
                        <div
                            className="tag tagSecond"
                            onClick={(e) => {
                                e.stopPropagation();
                                removeCollect(originDataObject.id);
                            }}
                        >
                            <span>
                                <LineOutlined />
                            </span>
                        </div>
                    </Tooltip>
                )}
            </Card>

            {/* <Card
                hoverable
                className="card"
                cover={
                    <img
                        alt="example"
                        src="https://img2.doubanio.com/view/photo/s_ratio_poster/public/p480747492.jpg"
                    />
                }
            >
                <p>111</p>
                <p>111</p>

                <div className="tag tagFirst">
                    <Tooltip title="">
                        <span>+</span>
                    </Tooltip>
                </div>
                <div className="tag tagSecond">
                    <Tooltip title="">
                        <span>-</span>
                    </Tooltip>
                </div>
            </Card> */}
        </div>
    );
}
