import  { useState ,useCallback} from "react";
import "./App.css";
import Header from "./component/Header";
import List from "./component/List";
import { Space, Button } from "antd";

interface Iprops {
    originData: [];
}

function App({ originData }: Iprops) {
    let [movie, setMovie] = useState(true);
    let [originMovieData, setOriginMovieData] = useState(originData);
    let [keyWord, setKeyWord] = useState("");
    let [sortByTime, setSortByTime] = useState(false);
    let [sortByRate, setSortByRate] = useState(false);

    
    const handlermovie=useCallback(function(flag: boolean) {
        setMovie(flag);
    },[])

    function collectMovies() {
        return originMovieData.filter((item: any) => !item.notOnCollect);
    }

    function addCollect(id: string) {
        // console.log(id);

        setOriginMovieData(
            originMovieData.map((item: any) => {
                if (item.id === id) item.notOnCollect = false;
                return item;
            }) as []
        );
    }

    function removeCollect(id: string) {
        // console.log(id);
        setOriginMovieData(
            originMovieData.map((item: any) => {
                if (item.id === id) item.notOnCollect = true;
                return item;
            }) as []
        );
    }

    // function searchMovies(keyWordFromSearch: string) {
    //     // console.log(keyWordFromSearch);

    //     setKeyWord(keyWordFromSearch);
    // }

    const searchMovies=useCallback(function(keyWordFromSearch: string) {
        setKeyWord(keyWordFromSearch);
    },[])

    function sortByTimeFun() {
        setSortByRate(false);
        setSortByTime(!sortByTime);
    }

    function sortByRateFun() {
        setSortByTime(false);
        setSortByRate(!sortByRate);
    }

    // console.log(originData);

    return (
        <div className="App">
            <Header
                movie={movie}
                handlermovie={handlermovie}
                searchMovies={searchMovies}
            ></Header>

            <Space size="middle" className="middle">
                <Button
                    onClick={sortByTimeFun}
                    className={sortByTime ? "active" : ""}
                >
                    
                </Button>
                <Button
                    onClick={sortByRateFun}
                    className={sortByRate ? "active" : ""}
                >
                    
                </Button>
            </Space>

            {/* <List originData={originData}></List> */}
            <List
                originMovieData={
                    movie ? originMovieData : (collectMovies() as [])
                }
                addCollect={addCollect}
                removeCollect={removeCollect}
                keyWord={keyWord}
                sortByTime={sortByTime}
                sortByRate={sortByRate}
            ></List>
        </div>
    );
}

export default App;
