import { Input, Col, Row, Button } from "antd";
import React from "react";

interface Iprops {
    movie: boolean;
    handlermovie: (flag: boolean) => void;
    searchMovies: (keyWord: string) => void;
}

function Header({ movie, handlermovie, searchMovies }: Iprops) {
    return (
        <Row justify="space-between" gutter={[0, 16]} className="header">
            <Col span={12}>
                <Input
                    addonBefore=":"
                    placeholder=""
                    allowClear
                    onPressEnter={(e: any) => searchMovies(e.target.value)}
                />
            </Col>
            <Col>
                <Button
                    className={movie ? "active" : ""}
                    onClick={() => handlermovie(true)}
                >
                    
                </Button>
            </Col>
            <Col>
                <Button
                    className={!movie ? "active" : ""}
                    onClick={() => handlermovie(false)}
                >
                    
                </Button>
            </Col>
        </Row>
    );
}

export default React.memo(Header)