import ReactDOM from "react-dom/client";
import App from "./App";
import axios from "axios";

const root = ReactDOM.createRoot(
    document.getElementById("root") as HTMLElement
);

axios({
    method: "get",
    url: "./data.json",
}).then(function (response) {
    let data = JSON.parse(
        JSON.stringify(response.data).replaceAll(/img\d/g, "img2")
    );
    data.forEach((element: any) => {
        element.notOnCollect = true;
    });
    root.render(
        // <React.StrictMode>
        <App originData={data} />
        // </React.StrictMode>
    );
});
